import React from 'react';
import './styles/header-pc.scss';
import './styles/header-mobile.scss';
import logo from '../../assets/images/logo.svg';

const Header = () => (
    <header>
        <div className="logo">
            <img src={logo} alt="CoAgent - автомобильные системы"/>
        </div>
    </header>
);

export default Header;